import defaults from '../defaults';
import { isUndefined } from 'mdui.jq/es/utils';
import { GET, POST, PUT, PATCH, DELETE } from './requestMethod';

if (isUndefined(defaults.adapter)) {
  throw new Error('adapter must be set. e.g. new BrowserAdapter() or new MiniProgramAdapter()');
}

const requestHandle = (method, url, data) => {
  const headers = {};
  const XHttpMethodOverride = 'X-Http-Method-Override';

  if (defaults.methodOverride) {
    if (method === PATCH || method === PUT) {
      headers[XHttpMethodOverride] = method;
      method = POST;
    }

    if (method === DELETE) {
      headers[XHttpMethodOverride] = method;
      method = GET;
    }
  } // header 中添加 accept


  const accepts = ['application/json'];

  if (typeof document !== 'undefined' && !![].map && document.createElement('canvas').toDataURL('image/webp').indexOf('data:image/webp') === 0) {
    accepts.push('image/webp');
  }

  headers['Accept'] = accepts.join(', ');
  return defaults.adapter.request({
    method,
    url,
    data,
    headers
  });
};

export const getRequest = (url, data) => requestHandle(GET, url, data);
export const postRequest = (url, data) => requestHandle(POST, url, data);
export const patchRequest = (url, data) => requestHandle(PATCH, url, data);
export const putRequest = (url, data) => requestHandle(PUT, url, data);
export const deleteRequest = (url, data) => requestHandle(DELETE, url, data);