import { h } from 'hyperapp';
import cc from 'classcat';
import { searchTypeMap } from '~/pages/options/dataMap';
import Select from '../../components/select/view.jsx';
import SaveBtn from '../../components/save-btn/view.jsx';
export default (({
  state,
  actions
}) => {
  const {
    data,
    submitting
  } = state;
  const {
    onSubmit
  } = actions;
  return h("form", {
    method: "post",
    onsubmit: onSubmit
  }, h(Select, {
    label: "\u641C\u7D22\u5F15\u64CE",
    name: "search_type",
    value: data.search_type,
    data: searchTypeMap,
    onChange: e => {
      data.search_type = e.target.value;
      actions.setState({
        data
      });
    }
  }), h("div", {
    class: cc([{
      'mdui-hidden': data.search_type !== 'third'
    }])
  }, h(Select, {
    label: "\u7B2C\u4E09\u65B9\u641C\u7D22\u5F15\u64CE\u540D\u79F0",
    name: "search_third",
    value: data.search_third,
    data: {
      google: 'Google',
      bing: 'Bing',
      sogou: '搜狗',
      '360': '360',
      baidu: '百度'
    }
  })), h(SaveBtn, {
    submitting: submitting
  }));
});