import { h } from 'hyperapp';
import { Link } from 'hyperapp-router';
import { fullPath } from '~/utils/path';
import { cacheTypeMap, storageTypeMap, searchTypeMap } from './dataMap';
import './index.less';
import Loading from '~/components/loading/view.jsx';

const Item = ({
  to,
  label,
  value = null
}) => h(Link, {
  to: fullPath(to),
  class: "item mdui-ripple"
}, h("div", {
  class: "label mdui-text-color-theme-text"
}, label), value ? h("div", {
  class: "value mdui-text-color-theme-secondary"
}, value) : null, h("i", {
  class: "arrow mdui-icon material-icons mdui-text-color-theme-icon"
}, "arrow_drop_down"));

const timeIn = ts => {
  if (ts < 60) {
    return `${ts} 秒内`;
  }

  if (ts < 3600) {
    return `${ts / 60} 分钟内`;
  }

  if (ts < 86400) {
    return `${ts / 3600} 小时内`;
  }

  return `${ts / 86400} 天内`;
};

export default ((state, actions) => ({
  match
}) => {
  const {
    data,
    loading
  } = state;
  return h("div", {
    oncreate: element => actions.onCreate({
      element
    }),
    ondestroy: element => actions.onDestroy({
      element
    }),
    key: match.url,
    class: "mdui-container",
    id: "page-options"
  }, h(Loading, {
    show: loading
  }), data ? [h("div", {
    class: "subheader subheader-first mdui-text-color-theme-secondary",
    key: "0"
  }, "\u7CFB\u7EDF\u8BBE\u7F6E"), h("div", {
    class: "mdui-card mdui-card-shadow",
    key: "1"
  }, h(Item, {
    to: "/options/info",
    label: "\u7AD9\u70B9\u4FE1\u606F",
    value: data.site_name
  }), h(Item, {
    to: "/options/theme",
    label: "\u4E3B\u9898",
    value: data.theme
  }), h(Item, {
    to: "/options/mail",
    label: "\u90AE\u4EF6",
    value: data.smtp_host
  }), h(Item, {
    to: "/options/cache",
    label: "\u7F13\u5B58",
    value: cacheTypeMap[data.cache_type]
  }), h(Item, {
    to: "/options/search",
    label: "\u641C\u7D22\u5F15\u64CE",
    value: searchTypeMap[data.search_type]
  }), h(Item, {
    to: "/options/upload",
    label: "\u6587\u4EF6\u4E0A\u4F20",
    value: storageTypeMap[data.storage_type]
  })), h("div", {
    class: "subheader mdui-text-color-theme-secondary",
    key: "2"
  }, "\u7F16\u8F91\u4E0E\u5220\u9664\u6743\u9650\u8BBE\u7F6E"), h("div", {
    class: "mdui-card mdui-card-shadow",
    key: "3"
  }, h(Item, {
    to: "/options/article_edit",
    label: "\u6587\u7AE0\u7F16\u8F91",
    value: // eslint-disable-next-line no-nested-ternary
    data.article_can_edit ? [data.article_can_edit_only_no_comment ? '无评论' : null, data.article_can_edit_before ? timeIn(data.article_can_edit_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/article_delete",
    label: "\u6587\u7AE0\u5220\u9664",
    value: data.article_can_delete ? [data.article_can_delete_only_no_comment ? '无评论' : null, data.article_can_delete_before ? timeIn(data.article_can_delete_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/question_edit",
    label: "\u63D0\u95EE\u7F16\u8F91",
    value: data.question_can_edit ? [data.question_can_edit_only_no_answer ? '无回答' : null, data.question_can_edit_only_no_comment ? '无评论' : null, data.question_can_edit_before ? timeIn(data.question_can_edit_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/question_delete",
    label: "\u63D0\u95EE\u5220\u9664",
    value: data.question_can_delete ? [data.question_can_delete_only_no_answer ? '无回答' : null, data.question_can_delete_only_no_comment ? '无评论' : null, data.question_can_delete_before ? timeIn(data.question_can_delete_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/answer_edit",
    label: "\u56DE\u7B54\u7F16\u8F91",
    value: data.answer_can_edit ? [data.answer_can_edit_only_no_comment ? '无回答' : null, data.answer_can_edit_before ? timeIn(data.answer_can_edit_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/answer_delete",
    label: "\u56DE\u7B54\u5220\u9664",
    value: data.answer_can_delete ? [data.answer_can_delete_only_no_comment ? '无回答' : null, data.answer_can_delete_before ? timeIn(data.answer_can_delete_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/comment_edit",
    label: "\u8BC4\u8BBA\u7F16\u8F91",
    value: data.comment_can_edit ? [data.comment_can_edit_before ? timeIn(data.comment_can_edit_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }), h(Item, {
    to: "/options/comment_delete",
    label: "\u8BC4\u8BBA\u5220\u9664",
    value: data.comment_can_delete ? [data.comment_can_delete_before ? timeIn(data.comment_can_delete_before) : null].filter(x => x).join(' 且 ') : '禁止'
  }))] : null);
});