import { isObjectLike, isUndefined, toCamelCase } from '../utils';
import each from './each';
import dataNS from './utils/data';
/**
 * 在元素上设置键值对数据
 * @param element
 * @param object
 */

function setObjectToElement(element, object) {
  // @ts-ignore
  if (!element[dataNS]) {
    // @ts-ignore
    element[dataNS] = {};
  }

  each(object, (key, value) => {
    // @ts-ignore
    element[dataNS][toCamelCase(key)] = value;
  });
}

function data(element, key, value) {
  // 根据键值对设置值
  // data(element, { 'key' : 'value' })
  if (isObjectLike(key)) {
    setObjectToElement(element, key);
    return key;
  } // 根据 key、value 设置值
  // data(element, 'key', 'value')


  if (!isUndefined(value)) {
    setObjectToElement(element, {
      [key]: value
    });
    return value;
  } // 获取所有值
  // data(element)


  if (isUndefined(key)) {
    // @ts-ignore
    return element[dataNS] ? element[dataNS] : {};
  } // 从 dataNS 中获取指定值
  // data(element, 'key')


  key = toCamelCase(key); // @ts-ignore

  if (element[dataNS] && key in element[dataNS]) {
    // @ts-ignore
    return element[dataNS][key];
  }

  return undefined;
}

export default data;