import { h } from 'hyperapp';
import { summaryText } from '~/utils/html';
import './index.less';
import Loading from '~/components/loading/view.jsx';

const Item = ({
  label,
  value
}) => h("a", {
  class: "mdui-list-item mdui-ripple"
}, h("div", {
  class: "mdui-list-item-content"
}, label), h("div", null, value));

export default (({
  state,
  actions
}) => {
  const {
    loading,
    topic
  } = state;
  return h("div", {
    class: "mc-topic mdui-dialog mdui-card",
    oncreate: actions.onCreate
  }, h(Loading, {
    show: loading
  }), topic ? [h("div", {
    class: "mdui-card-media",
    key: "0"
  }, h("img", {
    src: topic.cover.middle
  }), h("div", {
    class: "actions"
  }, h("button", {
    class: "to-edit mdui-fab mdui-fab-mini",
    "mdui-tooltip": "{content: '\u7F16\u8F91', delay: 300}",
    onclick: actions.toEdit
  }, h("i", {
    class: "mdui-icon material-icons"
  }, "edit")), h("button", {
    class: "delete mdui-fab mdui-fab-mini",
    "mdui-tooltip": "{content: '\u5220\u9664', delay: 300}",
    onclick: actions.delete
  }, h("i", {
    class: "mdui-icon material-icons"
  }, "delete")))), h("div", {
    class: "mdui-card-primary",
    key: "1"
  }, h("div", {
    class: "mdui-card-primary-title",
    oncreate: summaryText(topic.name),
    onupdate: summaryText(topic.name)
  })), h("div", {
    class: "mdui-card-content",
    oncreate: summaryText(topic.description),
    onupdate: summaryText(topic.description),
    key: "2"
  }), h("div", {
    class: "mdui-list mdui-list-dense mdui-text-color-theme-secondary",
    key: "3"
  }, h(Item, {
    label: "ID",
    value: topic.topic_id
  }), h(Item, {
    label: "\u5173\u6CE8\u8005\u6570\u91CF",
    value: topic.follower_count
  }), h(Item, {
    label: "\u6587\u7AE0\u6570\u91CF",
    value: topic.article_count
  }), h(Item, {
    label: "\u63D0\u95EE\u6570\u91CF",
    value: topic.question_count
  }))] : null);
});