import { h } from 'hyperapp';
import cc from 'classcat';
import mdui from 'mdui';
import $ from 'mdui.jq';
import { Link } from 'hyperapp-router';
import { fullPath } from '~/utils/path';
import './index.less';

const UserMenuItem = ({
  icon,
  name,
  href,
  onClick
}) => h("li", {
  class: "mdui-menu-item"
}, h("a", {
  class: "mdui-ripple",
  href: href,
  target: href ? '_blank' : false,
  onclick: onClick
}, h("i", {
  class: "mdui-menu-item-icon mdui-icon material-icons mdui-text-color-theme-icon"
}, icon), name));

export default (({
  state,
  actions
}, searchBar) => {
  const {
    shadow,
    user
  } = state;
  return h("div", {
    class: cc(['mc-appbar', 'mdui-appbar', 'mdui-appbar-fixed', {
      shadow
    }]),
    oncreate: element => actions.onCreate({
      element
    }),
    key: "mc-appbar"
  }, h("div", {
    class: "mdui-toolbar"
  }, h("button", {
    class: "drawer mdui-btn mdui-btn-icon mdui-ripple",
    oncreate: element => {
      const $drawer = $('.mc-drawer');
      const drawer = new mdui.Drawer($drawer, {
        swipe: true
      });
      $drawer.data('drawer-instance', drawer);
      $(element).on('click', () => drawer.toggle());
    }
  }, h("i", {
    class: "mdui-icon material-icons"
  }, "menu")), h(Link, {
    to: fullPath(''),
    class: "logo"
  }, h("i", {
    class: "mdui-icon material-icons"
  }, "dashboard"), "\u63A7\u5236\u53F0"), searchBar, user ? h("div", {
    class: "user"
  }, h("div", {
    class: "mdui-btn mdui-btn-icon mdui-btn-dense",
    title: `账号：${user.username}\n(${user.email})`,
    "mdui-menu": "{target: '#appbar-user-popover', covered: false}"
  }, h("img", {
    src: user.avatar.small,
    width: "32",
    height: "32"
  })), h("ul", {
    class: "mdui-menu",
    id: "appbar-user-popover"
  }, h(UserMenuItem, {
    name: "\u67E5\u770B\u7AD9\u70B9",
    icon: "home",
    href: `${window.G_ROOT}/`
  }), h(UserMenuItem, {
    name: "\u6211\u7684\u4E3B\u9875",
    icon: "person",
    href: `${window.G_ROOT}/users/${user.user_id}`
  }), h(UserMenuItem, {
    name: "\u9000\u51FA\u767B\u5F55",
    icon: "exit_to_app",
    onClick: actions.logout
  }))) : null));
});