import { getRequest, postRequest, patchRequest, deleteRequest } from './util/requestAlias';
import { buildURL, buildRequestBody } from './util/requestHandler';
/**
 * 🔑删除评论
 *
 * 只要没有错误异常，无论是否有回答被删除，该接口都会返回成功。  管理员可删除评论。评论作者是否可删除评论，由管理员在后台的设置决定。
 */

export const del = params => deleteRequest(buildURL('/comments/{comment_id}', params));
/**
 * 🔑为评论投票
 *
 * 为评论投票。
 */

export const addVote = params => postRequest(buildURL('/comments/{comment_id}/voters', params), buildRequestBody(params, ['type']));
/**
 * 🔑在指定评论下发表回复
 *
 * 在指定评论下发表回复。
 */

export const createReply = params => postRequest(buildURL('/comments/{comment_id}/replies', params, ['include']), buildRequestBody(params, ['content']));
/**
 * 🔐批量删除评论
 *
 * 批量删除评论。  只要没有错误异常，无论是否有评论被删除，该接口都会返回成功。
 */

export const deleteMultiple = params => deleteRequest(buildURL('/comments/{comment_ids}', params));
/**
 * 🔑取消为评论的投票
 *
 * 取消为评论的投票。
 */

export const deleteVote = params => deleteRequest(buildURL('/comments/{comment_id}/voters', params));
/**
 * 获取评论详情
 *
 * 获取评论详情。
 */

export const get = params => getRequest(buildURL('/comments/{comment_id}', params, ['include']));
/**
 * 🔐获取所有评论
 *
 * 获取所有评论。
 */

export const getList = (params = {}) => getRequest(buildURL('/comments', params, ['page', 'per_page', 'order', 'include', 'comment_id', 'commentable_id', 'commentable_type', 'user_id', 'trashed']));
/**
 * 获取指定评论的回复
 *
 * 获知指定评论的回复。
 */

export const getReplies = params => getRequest(buildURL('/comments/{comment_id}/replies', params, ['page', 'per_page', 'order', 'include']));
/**
 * 获取评论的投票者
 *
 * 获取评论的投票者。
 */

export const getVoters = params => getRequest(buildURL('/comments/{comment_id}/voters', params, ['page', 'per_page', 'include', 'type']));
/**
 * 🔐把评论放入回收站
 *
 * 把评论放入回收站。
 */

export const trash = params => postRequest(buildURL('/comments/{comment_id}/trash', params, ['include']));
/**
 * 🔐批量把评论放入回收站
 *
 * 批量把评论放入回收站。
 */

export const trashMultiple = params => postRequest(buildURL('/comments/{comment_ids}/trash', params, ['include']));
/**
 * 🔐把评论移出回收站
 *
 * 把评论移出回收站。
 */

export const untrash = params => postRequest(buildURL('/comments/{comment_id}/untrash', params, ['include']));
/**
 * 🔐批量把评论移出回收站
 *
 * 批量把评论移出回收站。
 */

export const untrashMultiple = params => postRequest(buildURL('/comments/{comment_ids}/untrash', params, ['include']));
/**
 * 🔑修改评论
 *
 * 管理员可修改评论。评论作者是否可修改评论，由管理员在后台的设置决定。
 */

export const update = params => patchRequest(buildURL('/comments/{comment_id}', params, ['include']), buildRequestBody(params, ['content']));