import { h } from 'hyperapp';
import $ from 'mdui.jq';

const valueFormat = value => {
  if (value < 60) {
    return value;
  }

  if (value < 3600) {
    return value / 60;
  }

  if (value < 86400) {
    return value / 3600;
  }

  return value / 86400;
};

const onInput = e => {
  const $currentInput = $(e.target);
  const $timeIn = $currentInput.parents('.time-in');
  const $inputNumber = $timeIn.find('.time-in-number');
  const $inputUnit = $timeIn.find('.time-in-unit');
  const $inputValue = $timeIn.find('.time-in-value');
  const value = parseInt($inputNumber.val(), 10) * parseInt($inputUnit.val(), 10);
  $inputValue.val(value);
};

export default (({
  name,
  value
}) => h("div", {
  class: "time-in"
}, "\u4EC5\u53D1\u5E03\u540E", h("div", {
  class: "mdui-textfield"
}, h("input", {
  class: "mdui-textfield-input time-in-number",
  type: "number",
  value: valueFormat(value),
  oninput: onInput
})), h("select", {
  class: "mdui-select time-in-unit",
  "mdui-select": true,
  oncreate: element => $(element).mutation(),
  onchange: onInput
}, h("option", {
  value: "1",
  selected: value > 0 && value < 60
}, "\u79D2"), h("option", {
  value: "60",
  selected: value === 0 || value >= 60 && value < 3600
}, "\u5206\u949F"), h("option", {
  value: "3600",
  selected: value >= 3600 && value < 86400
}, "\u5C0F\u65F6"), h("option", {
  value: "86400",
  selected: value >= 86400
}, "\u5929")), h("input", {
  type: "hidden",
  class: "time-in-value",
  name: name,
  value: value
}), "\u5185"));