import { h } from 'hyperapp';
import Select from '../../components/select/view.jsx';
import SaveBtn from '../../components/save-btn/view.jsx';
export default (({
  state,
  actions
}) => {
  const {
    data,
    submitting
  } = state;
  const {
    onSubmit
  } = actions;
  return h("form", {
    method: "post",
    onsubmit: onSubmit
  }, h(Select, {
    label: "\u4E3B\u9898",
    name: "theme",
    value: data.theme,
    data: {
      material: 'material'
    }
  }), h(SaveBtn, {
    submitting: submitting
  }));
});