import { getRequest, postRequest, patchRequest, deleteRequest } from './util/requestAlias';
import { buildURL, buildRequestBody } from './util/requestHandler';
/**
 * 🔑删除文章
 *
 * 只要没有错误异常，无论是否有文章被删除，该接口都会返回成功。  管理员可删除文章。文章作者是否可删除文章，由管理员在后台的设置决定。
 */

export const del = params => deleteRequest(buildURL('/articles/{article_id}', params));
/**
 * 🔑添加关注
 *
 * 添加关注。
 */

export const addFollow = params => postRequest(buildURL('/articles/{article_id}/followers', params));
/**
 * 🔑为文章投票
 *
 * 为文章投票。
 */

export const addVote = params => postRequest(buildURL('/articles/{article_id}/voters', params), buildRequestBody(params, ['type']));
/**
 * 🔑发表文章
 *
 * &#x60;content_markdown&#x60; 和 &#x60;content_rendered&#x60; 两个参数仅传入其中一个即可， 若两个参数都传入，则以 &#x60;content_markdown&#x60; 为准。
 */

export const create = params => postRequest(buildURL('/articles', params, ['include']), buildRequestBody(params, ['title', 'topic_ids', 'content_markdown', 'content_rendered']));
/**
 * 🔑在指定文章下发表评论
 *
 * 在指定文章下发表评论。
 */

export const createComment = params => postRequest(buildURL('/articles/{article_id}/comments', params, ['include']), buildRequestBody(params, ['content']));
/**
 * 🔑取消关注
 *
 * 取消关注。
 */

export const deleteFollow = params => deleteRequest(buildURL('/articles/{article_id}/followers', params));
/**
 * 🔐批量删除文章
 *
 * 批量删除文章。  只要没有错误异常，无论是否有文章被删除，该接口都会返回成功。
 */

export const deleteMultiple = params => deleteRequest(buildURL('/articles/{article_ids}', params));
/**
 * 🔑取消为文章的投票
 *
 * 取消为文章的投票。
 */

export const deleteVote = params => deleteRequest(buildURL('/articles/{article_id}/voters', params));
/**
 * 获取指定文章信息
 *
 * 获取指定文章信息。
 */

export const get = params => getRequest(buildURL('/articles/{article_id}', params, ['include']));
/**
 * 获取指定文章的评论列表
 *
 * 获取指定文章的评论列表。
 */

export const getComments = params => getRequest(buildURL('/articles/{article_id}/comments', params, ['page', 'per_page', 'order', 'include']));
/**
 * 获取指定文章的关注者
 *
 * 获取指定文章的关注者。
 */

export const getFollowers = params => getRequest(buildURL('/articles/{article_id}/followers', params, ['page', 'per_page', 'include']));
/**
 * 获取文章列表
 *
 * 获取文章列表。
 */

export const getList = (params = {}) => getRequest(buildURL('/articles', params, ['page', 'per_page', 'order', 'include', 'article_id', 'user_id', 'topic_id', 'trashed']));
/**
 * 获取文章的投票者
 *
 * 获取文章的投票者。
 */

export const getVoters = params => getRequest(buildURL('/articles/{article_id}/voters', params, ['page', 'per_page', 'include', 'type']));
/**
 * 🔐把文章放入回收站
 *
 * 把文章放入回收站。
 */

export const trash = params => postRequest(buildURL('/articles/{article_id}/trash', params, ['include']));
/**
 * 🔐批量把文章放入回收站
 *
 * 批量把文章放入回收站。
 */

export const trashMultiple = params => postRequest(buildURL('/articles/{article_ids}/trash', params, ['include']));
/**
 * 🔐把文章移出回收站
 *
 * 把文章移出回收站。
 */

export const untrash = params => postRequest(buildURL('/articles/{article_id}/untrash', params, ['include']));
/**
 * 🔐批量把文章移出回收站
 *
 * 批量把文章移出回收站。
 */

export const untrashMultiple = params => postRequest(buildURL('/articles/{article_ids}/untrash', params, ['include']));
/**
 * 🔑更新文章信息
 *
 * 管理员可修改文章。文章作者是否可修改文章，由管理员在后台的设置决定。  &#x60;content_markdown&#x60; 和 &#x60;content_rendered&#x60; 两个参数仅传入其中一个即可， 若两个参数都传入，则以 &#x60;content_markdown&#x60; 为准。
 */

export const update = params => patchRequest(buildURL('/articles/{article_id}', params, ['include']), buildRequestBody(params, ['title', 'topic_ids', 'content_markdown', 'content_rendered']));