import { h } from 'hyperapp';
import { Link } from 'hyperapp-router';
import './index.less';
export default (({
  topics
}) => h("div", {
  class: "mc-topics-bar"
}, topics.map(topic => h(Link, {
  to: `${window.G_ROOT}/topics/${topic.topic_id}`,
  class: "mdui-chip mdui-ripple",
  key: topic.topic_id,
  target: "_blank"
}, h("img", {
  class: "mdui-chip-icon",
  src: topic.cover.small
}), h("span", {
  class: "mdui-chip-title"
}, topic.name)))));