export default class {
  /**
   * 获取数据存储
   * @param key
   */
  getStorage(key) {
    return window.localStorage.getItem(key);
  }
  /**
   * 设置数据存储
   * @param key
   * @param data
   */


  setStorage(key, data) {
    window.localStorage.setItem(key, data);
  }
  /**
   * 删除数据存储
   * @param key
   */


  removeStorage(key) {
    window.localStorage.removeItem(key);
  }

}