import { h } from 'hyperapp';
import $ from 'mdui.jq';
import './index.less';
import Loading from '~/components/loading/view.jsx';

const CountItem = ({
  label,
  count
}) => h("div", {
  class: "number-item"
}, h("div", {
  class: "label mdui-text-color-theme-secondary"
}, label), h("div", {
  class: "number mdui-text-color-theme-text"
}, count));

const SystemInfoItem = ({
  label,
  value
}) => h("div", {
  class: "item"
}, h("label", null, label), h("span", {
  class: "mdui-text-color-theme-secondary"
}, value));

export default ((state, actions) => ({
  match
}) => {
  const {
    system_info
  } = state;

  const NewDataStats = ({
    title,
    type,
    newData
  }) => h("div", {
    class: "new-data mdui-card mdui-card-shadow"
  }, h("div", {
    class: "header"
  }, h("div", {
    class: "title mdui-text-color-theme-text"
  }, title), h("select", {
    class: "mdui-select",
    "mdui-select": true,
    disabled: newData.loading,
    oncreate: element => $(element).mutation(),
    onchange: e => actions.onDateChange({
      e,
      type
    })
  }, h("option", {
    value: "7day",
    selected: newData.range === '7day'
  }, "\u6700\u8FD1 7 \u5929"), h("option", {
    value: "this-month",
    selected: newData.range === 'this-month'
  }, "\u672C\u6708"), h("option", {
    value: "last-month",
    selected: newData.range === 'last-month'
  }, "\u4E0A\u6708"), h("option", {
    value: "30day",
    selected: newData.range === '30day'
  }, "\u6700\u8FD1 30 \u5929"), h("option", {
    value: "this-year",
    selected: newData.range === 'this-year'
  }, "\u4ECA\u5E74"), h("option", {
    value: "last-year",
    selected: newData.range === 'last-year'
  }, "\u53BB\u5E74"), h("option", {
    value: "365day",
    selected: newData.range === '365day'
  }, "\u6700\u8FD1 1 \u5E74"))), h(Loading, {
    show: newData.loading
  }), !newData.loading ? h("div", {
    class: "chart-wrapper",
    oncreate: element => actions.onChartCreate({
      element,
      data: newData.data
    })
  }) : null);

  return h("div", {
    oncreate: actions.onCreate,
    ondestroy: actions.onDestroy,
    key: match.url,
    class: "mdui-container",
    id: "page-index"
  }, h(Loading, {
    show: state.loading
  }), system_info ? [h("div", {
    class: "total-number mdui-card mdui-card-shadow",
    key: "0"
  }, h("div", {
    class: "header"
  }, h("div", {
    class: "title mdui-text-color-theme-text"
  }, "\u6570\u636E\u603B\u91CF")), h("div", {
    class: "rows"
  }, h(CountItem, {
    label: "\u7528\u6237\u91CF",
    count: state.total_user
  }), h(CountItem, {
    label: "\u6587\u7AE0\u91CF",
    count: state.total_article
  }), h(CountItem, {
    label: "\u63D0\u95EE\u91CF",
    count: state.total_question
  }), h(CountItem, {
    label: "\u56DE\u7B54\u91CF",
    count: state.total_answer
  }), h(CountItem, {
    label: "\u8BC4\u8BBA\u91CF",
    count: state.total_comment
  }))), h("div", {
    class: "system-info mdui-card mdui-card-shadow",
    key: "1"
  }, h("div", {
    class: "header"
  }, h("div", {
    class: "title mdui-text-color-theme-text"
  }, "\u7CFB\u7EDF\u4FE1\u606F")), h("div", {
    class: "items"
  }, h(SystemInfoItem, {
    label: "MDClub \u7248\u672C",
    value: system_info.mdclub_version
  }), h(SystemInfoItem, {
    label: "\u64CD\u4F5C\u7CFB\u7EDF",
    value: system_info.os_version
  }), h(SystemInfoItem, {
    label: "PHP \u7248\u672C",
    value: system_info.php_version
  }), h(SystemInfoItem, {
    label: "Web \u670D\u52A1\u5668\u8F6F\u4EF6",
    value: system_info.webserver_version
  }), h(SystemInfoItem, {
    label: "\u6570\u636E\u5E93\u7248\u672C",
    value: system_info.database_version
  }), h(SystemInfoItem, {
    label: "\u4E0A\u4F20\u6587\u4EF6\u9650\u5236",
    value: system_info.upload_max_filesize
  }), h(SystemInfoItem, {
    label: "PHP \u6267\u884C\u65F6\u957F\u9650\u5236",
    value: system_info.max_execution_time
  }), h(SystemInfoItem, {
    label: "\u5269\u4F59\u786C\u76D8\u7A7A\u95F4",
    value: system_info.disk_free_space
  }), h(SystemInfoItem, {
    label: "\u6570\u636E\u5E93\u5927\u5C0F",
    value: system_info.database_size
  }))), h(NewDataStats, {
    title: "\u7528\u6237\u589E\u957F",
    type: "new_user",
    newData: state.new_user,
    key: "2"
  }), h(NewDataStats, {
    title: "\u6587\u7AE0\u6570\u589E\u957F",
    type: "new_article",
    newData: state.new_article,
    key: "3"
  }), h(NewDataStats, {
    title: "\u63D0\u95EE\u6570\u589E\u957F",
    type: "new_question",
    newData: state.new_question,
    key: "4"
  }), h(NewDataStats, {
    title: "\u56DE\u7B54\u6570\u589E\u957F",
    type: "new_answer",
    newData: state.new_answer,
    key: "5"
  }), h(NewDataStats, {
    title: "\u8BC4\u8BBA\u6570\u589E\u957F",
    type: "new_comment",
    newData: state.new_comment,
    key: "6"
  })] : null);
});