import { getRequest, postRequest, patchRequest, deleteRequest } from './util/requestAlias';
import { buildURL, buildRequestBody } from './util/requestHandler';
/**
 * 🔑删除提问
 *
 * 只要没有错误异常，无论是否有回答被删除，该接口都会返回成功。  管理员可删除提问。提问作者是否可删除提问，由管理员在后台的设置决定。
 */

export const del = params => deleteRequest(buildURL('/questions/{question_id}', params));
/**
 * 🔑添加关注
 *
 * 添加关注。
 */

export const addFollow = params => postRequest(buildURL('/questions/{question_id}/followers', params));
/**
 * 🔑为提问投票
 *
 * 为提问投票。
 */

export const addVote = params => postRequest(buildURL('/questions/{question_id}/voters', params), buildRequestBody(params, ['type']));
/**
 * 🔑发表提问
 *
 * &#x60;content_markdown&#x60; 和 &#x60;content_rendered&#x60; 两个参数仅传入其中一个即可， 若两个参数都传入，则以 &#x60;content_markdown&#x60; 为准。
 */

export const create = params => postRequest(buildURL('/questions', params, ['include']), buildRequestBody(params, ['title', 'topic_ids', 'content_markdown', 'content_rendered']));
/**
 * 🔑在指定提问下发表回答
 *
 * &#x60;content_markdown&#x60; 和 &#x60;content_rendered&#x60; 两个参数仅传入其中一个即可， 若两个参数都传入，则以 &#x60;content_markdown&#x60; 为准。
 */

export const createAnswer = params => postRequest(buildURL('/questions/{question_id}/answers', params, ['include']), buildRequestBody(params, ['content_markdown', 'content_rendered']));
/**
 * 🔑在指定提问下发表评论
 *
 * 在指定提问下发表评论。
 */

export const createComment = params => postRequest(buildURL('/questions/{question_id}/comments', params, ['include']), buildRequestBody(params, ['content']));
/**
 * 🔑取消关注
 *
 * 取消关注。
 */

export const deleteFollow = params => deleteRequest(buildURL('/questions/{question_id}/followers', params));
/**
 * 🔐批量删除提问
 *
 * 批量删除提问。  只要没有错误异常，无论是否有提问被删除，该接口都会返回成功。
 */

export const deleteMultiple = params => deleteRequest(buildURL('/questions/{question_ids}', params));
/**
 * 🔑取消为提问的投票
 *
 * 取消为提问的投票。
 */

export const deleteVote = params => deleteRequest(buildURL('/questions/{question_id}/voters', params));
/**
 * 获取指定提问信息
 *
 * 获取指定提问信息。
 */

export const get = params => getRequest(buildURL('/questions/{question_id}', params, ['include']));
/**
 * 获取指定提问下的回答
 *
 * 获取指定提问下的回答。
 */

export const getAnswers = params => getRequest(buildURL('/questions/{question_id}/answers', params, ['page', 'per_page', 'order', 'include']));
/**
 * 获取指定提问的评论
 *
 * 获取指定提问的评论。
 */

export const getComments = params => getRequest(buildURL('/questions/{question_id}/comments', params, ['page', 'per_page', 'order', 'include']));
/**
 * 获取指定提问的关注者
 *
 * 获取指定提问的关注者。
 */

export const getFollowers = params => getRequest(buildURL('/questions/{question_id}/followers', params, ['page', 'per_page', 'include']));
/**
 * 获取提问列表
 *
 * 获取提问列表。
 */

export const getList = (params = {}) => getRequest(buildURL('/questions', params, ['page', 'per_page', 'order', 'include', 'question_id', 'user_id', 'topic_id', 'trashed']));
/**
 * 获取提问的投票者
 *
 * 获取提问的投票者。
 */

export const getVoters = params => getRequest(buildURL('/questions/{question_id}/voters', params, ['page', 'per_page', 'include', 'type']));
/**
 * 🔐把提问放入回收站
 *
 * 把提问放入回收站。
 */

export const trash = params => postRequest(buildURL('/questions/{question_id}/trash', params, ['include']));
/**
 * 🔐批量把提问放入回收站
 *
 * 批量把提问放入回收站。
 */

export const trashMultiple = params => postRequest(buildURL('/questions/{question_ids}/trash', params, ['include']));
/**
 * 🔐把提问移出回收站
 *
 * 把提问移出回收站。
 */

export const untrash = params => postRequest(buildURL('/questions/{question_id}/untrash', params, ['include']));
/**
 * 🔐批量把提问移出回收站
 *
 * 批量把提问移出回收站。
 */

export const untrashMultiple = params => postRequest(buildURL('/questions/{question_ids}/untrash', params, ['include']));
/**
 * 🔑更新提问信息
 *
 * 管理员可修改提问。提问作者是否可修改提问，由管理员在后台的设置决定。  &#x60;content_markdown&#x60; 和 &#x60;content_rendered&#x60; 两个参数仅传入其中一个即可， 若两个参数都传入，则以 &#x60;content_markdown&#x60; 为准。
 */

export const update = params => patchRequest(buildURL('/questions/{question_id}', params, ['include']), buildRequestBody(params, ['title', 'topic_ids', 'content_markdown', 'content_rendered']));