const ItemText = ({
  subheader,
  title,
  content,
  type = 'string',
  multiLine = false
}) => {
  let contentFunc;

  if (multiLine) {
    contentFunc = plainText(content);
  } else {
    contentFunc = type === 'html' ? richText(content) : summaryText(content);
  }

  return h("div", {
    class: "text-item"
  }, h("div", {
    class: "text-subheader mdui-text-color-theme-secondary"
  }, subheader), h("div", {
    class: cc(['text-content', {
      'mdui-typo': multiLine
    }]),
    title: title,
    oncreate: contentFunc,
    onupdate: contentFunc
  }));
};

