import { h } from 'hyperapp';
import cc from 'classcat';
import { unescape } from 'html-escaper';
import { isUndefined } from 'mdui.jq/es/utils';
import { timeFormat, timeFriendly } from '~/utils/time';
import './index.less';
import Loading from '~/components/loading/view.jsx';
import Empty from '~/components/empty/view.jsx';
import Pagination from '~/components/pagination/view.jsx';

const CheckAll = ({
  isChecked,
  onChange
}) => h("th", {
  class: "mdui-table-cell-checkbox"
}, h("label", {
  class: "mdui-checkbox"
}, h("input", {
  type: "checkbox",
  checked: isChecked,
  onchange: onChange
}), h("i", {
  class: "mdui-checkbox-icon"
})));

const CheckOne = ({
  isChecked,
  onChange,
  avatar = false
}) => h("td", {
  class: cc(['mdui-table-cell-checkbox', {
    'with-avatar': avatar
  }])
}, avatar ? h("img", {
  class: "avatar",
  src: avatar
}) : null, h("label", {
  class: "mdui-checkbox"
}, h("input", {
  type: "checkbox",
  checked: isChecked,
  onchange: onChange
}), h("i", {
  class: "mdui-checkbox-icon"
})));

const ActionBtn = ({
  icon,
  label,
  href,
  onClick
}) => h("a", {
  class: "mdui-btn mdui-btn-icon mdui-text-color-theme-icon",
  "mdui-tooltip": `{content: '${label}', delay: 300}`,
  href: href,
  target: href ? '_blank' : false,
  onclick: onClick
}, h("i", {
  class: "mdui-icon material-icons"
}, icon));

const ColumnTH = ({
  column
}) => h("th", {
  key: column.field,
  class: cc([{
    'mdui-table-col-numeric': column.type === 'number'
  }]),
  style: column.width && `width: ${column.width}px;`
}, column.title);

const ColumnTHChecked = ({
  state
}) => h("th", {
  colspan: state.columns.length
}, h("span", null, state.batchButtons.map(button => h(ActionBtn, {
  label: button.label,
  icon: button.icon,
  onClick: () => {
    const items = [];
    state.data.map(item => {
      if (state.isCheckedRows[item[state.primaryKey]]) {
        items.push(item);
      }
    });
    button.onClick(items);
  }
}))), h("span", {
  class: "mdui-float-right"
}, "\u5DF2\u9009\u62E9 ", state.checkedCount, " \u4E2A\u9879\u76EE"));

const ColumnTdText = ({
  column,
  row
}) => {
  /* eslint-disable no-eval */
  const value = eval(`row.${column.field}`);

  if (isUndefined(value)) {
    return null;
  }

  switch (column.type) {
    case 'time':
      return h("span", {
        title: timeFormat(value)
      }, timeFriendly(value));

    case 'relation':
      return h("a", {
        onclick: e => column.onClick({
          e,
          row
        })
      }, unescape(value));

    case 'handler':
      return unescape(column.handler(row));

    case 'number':
      return value;

    default:
      return unescape(value);
  }
};

const ColumnTD = ({
  column,
  row
}) => {
  const style = column.width && `width: ${column.width}px;`;

  switch (column.type) {
    case 'number':
      return h("td", {
        class: "mdui-table-col-numeric",
        style: style
      }, h(ColumnTdText, {
        column: column,
        row: row
      }));

    default:
      return h("td", {
        style: style
      }, h(ColumnTdText, {
        column: column,
        row: row
      }));
  }
};

const ColumnTDAction = ({
  button,
  row
}) => {
  switch (button.type) {
    case 'target':
      return h(ActionBtn, {
        icon: "open_in_new",
        label: "\u65B0\u7A97\u53E3\u6253\u5F00",
        href: button.getTargetLink(row)
      });

    case 'btn':
      return h(ActionBtn, {
        icon: button.icon,
        label: button.label,
        onClick: e => {
          e.preventDefault();
          button.onClick(row);
        }
      });

    default:
      return null;
  }
};

export default (({
  loadData
}) => ({
  datatable: state
}, {
  datatable: actions
}) => {
  const isEmpty = !state.loading && !state.data.length;
  const isLoading = state.loading;
  return h("div", {
    class: cc(['mc-datatable', {
      'is-top': !state.isScrollTop
    }]),
    oncreate: element => actions.onCreate({
      element
    }),
    ondestroy: element => actions.onDestroy({
      element
    })
  }, h("table", {
    class: "mdui-table"
  }, h("thead", null, h("tr", {
    class: cc([{
      checked: state.checkedCount
    }])
  }, h(CheckAll, {
    isChecked: state.isCheckedAll,
    onChange: e => actions.checkAll(e)
  }), state.checkedCount ? h(ColumnTHChecked, {
    state: state
  }) : null, !state.checkedCount ? [state.columns.map((column, index) => {
    if (index !== state.columns.length - 1) {
      return h(ColumnTH, {
        column: column
      });
    }

    return null;
  }), h("th", {
    class: "actions",
    key: "1"
  }, h(Pagination, {
    onChange: loadData,
    loading: state.loading,
    orders: state.orders,
    order: state.order,
    changeOrder: actions.changeOrder,
    afterChangeOrder: loadData
  }))] : null)), h(Loading, {
    show: isLoading
  }), h(Empty, {
    show: isEmpty
  }), h("tbody", {
    class: cc([{
      'is-loading': isLoading,
      'is-empty': isEmpty
    }])
  }, state.data.map(row => h("tr", {
    key: row[state.primaryKey],
    "data-id": row[state.primaryKey],
    class: cc([{
      'mdui-table-row-selected': state.isCheckedRows[row[state.primaryKey]],
      'last-visit': state.lastVisitId === row[state.primaryKey]
    }]),
    onclick: e => {
      if (typeof state.onRowClick === 'function' && e.target.nodeName === 'TD') {
        state.onRowClick(row);
      }
    }
  }, h(CheckOne, {
    isChecked: state.isCheckedRows[row[state.primaryKey]],
    onChange: () => actions.checkOne(row[state.primaryKey]),
    avatar: row.avatar ? row.avatar.small : false
  }), state.columns.map((column, index) => {
    if (index !== state.columns.length - 1) {
      return h(ColumnTD, {
        column: column,
        row: row
      });
    }

    return null;
  }), h("td", {
    class: "actions",
    style: `width: ${state.columns[state.columns.length - 1].width}px;`
  }, h("span", {
    class: "placeholder"
  }, h(ColumnTdText, {
    column: state.columns[state.columns.length - 1],
    row: row
  })), state.buttons.map(button => h(ColumnTDAction, {
    button: button,
    row: row
  }))))))));
});