import $ from '../$';
import { isString } from '../utils';
import './children';
import './eq';
import './get';
import './parent';

$.fn.index = function (selector) {
  if (!arguments.length) {
    return this.eq(0).parent().children().get().indexOf(this[0]);
  }

  if (isString(selector)) {
    return $(selector).get().indexOf(this[0]);
  }

  return this.get().indexOf($(selector)[0]);
};