import CommonAbstract from './commonAbstract';

class MenuAbstract extends CommonAbstract {
  /**
   * @param editor 编辑器实例
   * @param $button 按钮 JQ 对象
   */
  constructor(editor, $button) {
    super(editor);
    this.$button = $button;
  }
  /**
   * 按钮是否激活
   */


  isActive() {
    return false;
  }

}
/**
 * 按钮图标
 */


MenuAbstract.icon = '';
/**
 * 按钮名称
 */

MenuAbstract.title = '';
/**
 * 激活按钮时，需要禁用的其他按钮
 */

MenuAbstract.disable = [];
export default MenuAbstract;